.slide-cont {
    /*height: 20px;*/
    background-color: black;
    display: flex;
    justify-content: space-around;
    margin: 52px auto 0;
    width: 100%;
    position: fixed;
    z-index: 90;
    max-height: 24px;
}

.title {
    color: var(--primary-color);
    font-size: min(3em,18px);
    padding-top: 4px;
    height: 18px;
    /*background-color: orange;*/
    /*margin-bottom: 18px;*/
}


