.order-cont {
    /*background-color: gray;*/
    margin: min(8vw, 16px) auto 0;
    width: min(90vw, 485px);
    color: var(--thirdary-color);
    text-transform: capitalize;
}

.order-cont h1 {
    margin-top: min(5vw, 24px);
    color: var(--primary-color);
}

.ship-brief {
    display: flex;
    /* background-color: orange; */
    justify-content: space-between;
    /* width: 60vw; */
}

.ship-brief svg {
    color: var(--primary-color);
    background-color: var(--thirdary-color);
    height: min(3em, 66px);
    width: min(3em, 66px);
    padding: 12px;
    border-radius: 12px;
}

.ship-details {
    /* background-color: green; */
}

.ship-details h3,
h6 {
    font-size: min(1.2rem, 26px) !important;
    text-align: end;
    margin-bottom: 6px;
}

.prod-summary {
    display: grid;
    row-gap: min(5vw, 18px);
}

.placed {
    font-size: 1em;
}

.cat {
    display: flex;
    justify-content: space-between;
}

/*MEDIA_QUERIES*/
@media only screen and (min-width: 425px) {
    /*.product-div{margin-bottom: -360px;}*/
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 540px) {
    .order-cont h1 {
        font-size: 36px
    }

    .order-cont h3 {
        font-size: 32px
    }

    .placed {
        font-size: 22px
    }
}

