.track-cont {
 height: min(175vw,900px);
  /* overflow-y: hidden; */
  background: linear-gradient(
    /* to right, */ var(--primary-color),
    var(--secondary-color),
    80%,
    var(--thirdary-color) /* var(--fourthdary-color) */
  );
  margin-bottom: -10vw;
}
.on-form {
}
.center-div {
  width: fit-content;
  /* background-color: green; */
  margin: 0 auto;
  padding-top: min(20vw,128px);
  padding-botttom: min(20vw,128px);
  display: grid;
  row-gap: 10vw;
}
.center-div h2 {
  text-align: center;
}
.lbl {
  display: flex;
  flex-direction: column;
}
.lbl label {
  font-size: min(2em,42px);
}
.lbl input {
  background-color: transparent;
  border: none;
  border-bottom: 2px solid black;
  margin-bottom: min(5vw,26px);

}
form {
  /* background-color: gray; */
}
form button {
  font-size: min(1.5em,32px);
  background-color: var(--primary-color) !important;
  border-radius: 12px !important;
}
/*MEDIA_QUERIES*/
@media only screen and (min-width: 425px) {
.track-cont{margin-top: -11vw}
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 540px) {
.center-div{
  width: 230px;
}
  .lbl input{
    font-size: min(2em,42px);

  }

}
