.prod-cont img {
  width: min(25vw,136px);
  height: min(25vw,136px);
  border-radius: min(3vw,16px);
}
.prod-cont {
  display: flex;
  justify-content: space-between;
  text-align: end;
  width: 95%;
  margin: 0 auto;
}
.prod-cont h3{
  max-font-size: 32px;
  margin-bottom: 12px;
}

