.main-product {
    color: var(--thirdary-color);
    width: 90vw;
    min-height: 140vw;
     /*background-color: aqua;*/
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    text-align: center;
    /* justify-content: space-between; */
}

.main-product > img {
    border-radius: min(1em,12px);

    box-shadow: 0rem 1rem 2rem var(--thirdary-color);
    margin: 0 auto;
    width: min(70vw,348px);
    background-color: orange;
}

.main-product h1 {
    font-size: min(3.5rem,56px);
    margin-top: 3vw;
    height: fit-content;
}

.main-product h2 {
    font-size: min(2rem,36px);
    /* background-color: black; */
    height: fit-content;
}

.main-product h4 {
    font-size: min(1.5em,30px);

}


.opt-images {
    display: flex;
    width: min(70vw,348px);
    justify-content: space-between;
    margin: min(5vw, 24px) auto 0;
}

.mini-img {
    width: min(20vw,100px);
    height: min(20vw,100px);
    border-radius: min(1em,12px);
}

