.reccomended-grid {
    display: grid;
    color: var(--secondary-color);

    grid-template-columns: minmax(20vw, 30vw) auto auto auto;
    column-gap: 1rem;
    width: fit-content;
    margin: 0 auto;
}

.reccomended-grid img {
    border-radius: min(1rem,18px);
    width: min(20vw,150px);
    height: min(20vw,150px);
    box-shadow: 0rem 1rem 2rem var(--thirdary-color);
    /* height: 70vw; */
}

.rec-item-div {
    text-align: center;
    margin: 0 auto;
    display: block;
    /* background-color: black; */
}

.rec-item-div p {
    margin: 0;
}

.rec-item-div img {
    margin-bottom: 3vw;
}


