.canvas {
    background-color: black;
    width: min(80vw, 400px);
    position: absolute;
    right: 0;
    height: 100vh;
    top: 0;
    transform: translateX(100%);
    -webkit-transform: translateX(100%);
    color: var(--fourthdary-color);
    overflow-y: scroll;
    padding-left: 12px;
    z-index: 99 !important;
}


.selected {
    animation: slide-in 0.5s forwards;
    -webkit-animation: slide-in 0.5s forwards;
}

.dismiss {
    animation: slide-out 0.5s forwards;
    -webkit-animation: slide-out 0.5s forwards;
}

@keyframes slide-in {
    0% {
        -webkit-transform: translateX(100%);
    }
    100% {
        -webkit-transform: translateX(0%);
    }
}

@-webkit-keyframes slide-in {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(0%);
    }
}

@keyframes slide-out {
    0% {
        transform: translateX(0%);
    }
    100% {
        transform: translateX(100%);
    }
}

@-webkit-keyframes slide-out {
    0% {
        -webkit-transform: translateX(0%);
    }
    100% {
        -webkit-transform: translateX(100%);
    }
}

.cross-icon {
    fill: var(--primary-color);
    right: min(3vw, 24px);
    top: min(3vw, 24px);
    position: absolute;
    width: min(1.5em, 36px);
    height: min(1.5em, 36px);
    cursor: pointer;
}

.title {
    font-size: min(2rem, 36px);

    font-weight: 800;
    color: var(--primary-color);
    text-align: center;
    margin: min(10vw, 74px) auto 0;
    margin-bottom: min(3vw, 16px);

}

.total-cont {
    display: flex;
    justify-content: space-between;
    width: 95%;
    margin: 0 auto;
    /*margin-top: min(15vw,24px);*/
    /*margin-bottom: 5vw;*/
    /* background-color: green; */
}

.total-cont h3 {
    font-weight: 800 !important;
    font-size: min(6.5vw, 32px);
}

.total-cont h2 {
    color: var(--primary-color);
    font-size: min(6.5vw, 32px);
}

.misc-cont {
    position: absolute;
    width: 95%;
    /*bottom: 0;*/
    margin-top: 24px;
}

.go_cart {
    color: var(--fourthdary-color) !important;
    font-weight: 800;
    font-size: min(5vw, 27px);
    width: min(95%, 364px);
    padding-top: 1vw;
    padding-bottom: 1vw;
    border: 3px solid !important;
    background-color: var(--primary-color) !important;
    display: block;
    margin: 0 auto;
}

.go_cart div {
    width: min(60%, 207px);
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
}

.go_cart div > span {
    max-font-size: 27px;
}

.check-out-icn {
    width: min(1.5em, 36px);
    height: min(1.5em, 36px);
}

.go_cart:hover {
    border-color: var(--primary-color) !important;
    background-color: black !important;
}

.badge {
    padding: 0 3px;
    background-color: var(--secondary-color);
    font-size: min(16px, 24px);
    position: absolute;
    top: 3vw;
    right: 3vw;
    width: 4vw;
    max-width: 24px;
    border-radius: 24px;
}

