.footer-container {

    background-color: var(--primary-color);
    display: grid;
    padding-top: min(5vw,12px);
    padding-bottom: min(5vw,12px);
    margin-top: 5vw;

}

.icons-container {
    color: var(--fourthdary-color);
    width: 45vw;
    margin: 0 auto;
    display: flex;
    justify-content: space-evenly;
    /* background-color: green; */
}

.icon {
    color: var(--fourthdary-color);
    fill: var(--fourthdary-color);
    width: min(2rem, 42px);
}

.footer-container h1 {
    font-size: min(2.5em, 36px);
    text-align: center;
    color: var(--fourthdary-color);
    margin-bottom: 12px;
    /*margin-top: 5vw;*/
}

.footer-container p {
    font-size: min(1em, 22px);
    text-align: center;
    margin-bottom: 12px;
    /*margin-top: 0;*/
}

.icons-container a > svg:hover {
    color: black !important;
}

.hyper-links {
    width: 60vw;
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
}

.hyper-links > a {
    font-size: min(1em, 22px);
}
