button {
    background-color: transparent !important;
    border: none !important;
    color: var(--fourthdary-color) !important;
}

.svg-container:hover {
    /* color: black !important; */
}

.navigation {
    background-color: var(--primary-color);
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    width: 100%;
    position: fixed;
    z-index: 92;
    max-height: 62px;
    height: 52px;
}

.navigation svg:hover {
    color: black;
}

.home-link {

    height: fit-content;
    width: fit-content;

}

.navbar-title {
    margin-top: min(3vw, 8px);
    font-size: min(2rem, 36px);
    color: var(--fourthdary-color);
}


