@font-face {
  font-family: Barbie-Font;
  src: url("../src/assets/fonts/barbie.ttf");
}
:root {
  --primary-color: #eb0f80;
  --secondary-color: #ee6dba;
  --thirdary-color: #f8b2e4;
  --fourthdary-color: #f9eaf8;
}
html {
  font-size: 4vw;
  color: black;
}
svg {
  width: 2rem;
  height: 2rem;
}
body {
  font-family: Barbie-Font, monospace;
}
a {
  text-decoration: none;
  color: black;
}
a:hover {
  color: inherit;
}
.offcanvas {
  min-width: 80vw !important;
  background-color: black;
}
.offcanvas-left {
  max-width: 60vw;
}
#root {
  padding-top: 16vw;
}
/* svg:hover {
  color: black;
} */
button {
  border-radius: 2vw !important;
}
/* scroll bar */
::-webkit-scrollbar {
  max-width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--primary-color);

  border-radius: 20vw;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--primary-color);
}
