.form-container{
    /*background-color: black;*/
}
.quantity-form {
    display: grid;
    /*width: 27vw;*/
    margin: 0 auto;
    grid-template-columns: auto auto;
    /*background-color: green;*/
    height: 56px;
    position: relative;
    margin-bottom: min(1vw,24px);

}

.qty_nm {
    margin-top: 2.5vw;
    margin-right: 1vw;
}
.button-container {
    display: flex;


}

.button-container button {
    background-color: transparent !important;
}

.button-container button svg {
    background-color: var(--primary-color) !important;
    height: min(5vw, 36px);
    width: min(5vw, 36px);
    padding: 1vw;
    border: 1px var(--primary-color) solid !important;
    border-radius: min(1vw,6px);
    margin: 0 !important;
}

.quantity-form input {
    width: 5vw !important;
    background-color: transparent !important;
    border: none;
    font-size: 1.5em;
}

.submit-button {
    white-space: nowrap;
    font-size: min(1em, 32px);
    margin: 0 auto;
    width: min(30vw, 250px);
    background-color: var(--primary-color) !important;
    border: solid 2px var(--primary-color) !important;
    border-radius: min(1vw,12px) !important;


}


