.product-div {
    display: flex;
    text-align: end;
    justify-content: space-between;
    width: 95%;
    margin-bottom: min(5vw, 24px);
}

.product-div h1 {
    font-weight: 800 !important;
    font-size: min(6.5vw, 32px);

    /* white-space: nowrap; */
}

.product-div h2 {
    font-weight: 600;
    font-size: min(5vw, 24px);
}

.product-div p {
    font-weight: 600;
    font-size: min(5vw, 24px);
    margin-bottom: 0;

}

.product-div img {
    width: min(20vw, 100px);
    height: min(20vw, 100px);
    border-radius: min(3vw, 12px);
    box-shadow: 0px 3px 12px var(--primary-color);
}

.side_cart-grid {
    display: flex;
    flex-direction: column;
    height: fit-content;
    overflow-y: auto;
}

p.remove-btn {
    font-size: min(3vw, 18px);
    margin-top: 0;
    color: var(--primary-color);
    width: fit-content;
    float: right;
    /* background-color: white; */
    text-align: right;
    cursor: pointer;

}


