.quantity-form {
  display: flex;
  width: 27vw;
  margin: 0 auto;
  justify-content: space-between;
}
.button-container {
  display: flex;
}
.button-container button {
  background-color: transparent !important;
}
.button-container button svg {
  background-color: var(--primary-color) !important;
  height: 8vw;
  width: 8vw;
  padding: 2vw;
  border: 1px var(--primary-color) solid !important;
  border-radius: 1vw;
  margin: 0 !important;
}
.quantity-form input {
  width: 5vw !important;
  background-color: transparent !important;
  border: none;
  font-size: 1.5em;
}

.submit-button {
  white-space: nowrap;
  font-size: min(1.7em,32px);
  margin: 5vw auto;
  width: min(75%,300px);
  background-color: var(--primary-color) !important;
  border: solid 2px var(--primary-color) !important;
}
.submit-button:hover {
  background-color: black !important;
}


