.order-placed-cont {
    height: min(180vw,900px);
    /* overflow-y: hidden; */
    background: linear-gradient(
        /* to right, */ var(--primary-color),
            var(--secondary-color),
            80%,
            var(--thirdary-color) /* var(--fourthdary-color) */
    );
    margin-bottom: -10vw;
}

.text-cont {
    /* background-color: orange; */
    width: fit-content;
    text-align: center;
    display: grid;
    row-gap: 24px;
}

.text-cont h1 {
    font-size: min(2.5em, 48px);
}

.text-cont h2 {
    font-size: min(2.5em, 32px);
}

.order-placed-cont svg {
    width: min(6em, 120px);
    height: min(6em, 120px);
    color: var(--fourthdary-color);
    margin: 0 auto;
    display: block;
}

.success-icon {
    color: var(--primary-color);
    height: 5em;
}

.g2_Order {
    background-color: var(--primary-color) !important;
    display: flex;
    justify-content: space-around;
    width: min(50vw, 270px);
    margin: 0 auto;
    padding-top: min(2vw, 6px);
    padding-bottom: min(2vw, 6px);
    border: solid 2px var(--primary-color) !important;
    max-height: 65px;
    border-radius: 12px !important;
}

.g2_Order > h3 {
    font-size: min(1.5rem, 32px);
}

.g2_Order svg {
    width: min(2rem, 44px);
    height: min(2rem, 44px);
    /* background-color: white; */
}

.g2_Order:hover {
    background-color: black !important;
    border: solid 2px var(--primary-color) !important;
}

.inner-cont {
    display: grid;
    row-gap: 24px;
    position: absolute;
    left: 50%;
    top: 50%;
    width: min(95vw, 400px);
    /* background-color: orange; */
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

