.outer-prod-container {
    width: fit-content;
    margin: min(20vw,56px) auto 0;
}

.product-grid {
    display: grid;
    color: var(--secondary-color);
    grid-template-rows: minmax(70vw, 80vw) repeat(auto);
    grid-template-columns: min(60vw, 310px);
    row-gap: 2rem;
    margin: 0 auto;
}

/*MEDIA_QUERIES*/
@media only screen and (min-width: 425px) {
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 540px) {
    .product-grid {
        grid-template-columns: 30vw 30vw;
        grid-template-rows: auto;
        column-gap: min(15vw, 92px);
    }


}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 740px) {
    .product-grid {
        grid-template-columns:  min(20vw,220px) min(20vw,220px) min(20vw,220px);
        grid-template-rows: auto;
        column-gap: min(15vw, 92px);
    }

}

