.main-product {
    color: var(--thirdary-color);
    width: min(80vw, 750px);
    /*min-height: 140vw;*/
    /*background-color: aqua;*/
    margin: 0 auto;
    display: flex;
    /*flex-direction: column;*/
    text-align: left;
    justify-content: space-between;
}

.main-img {
    border-radius: min(1em, 12px);
    box-shadow: 0 1rem 2rem var(--thirdary-color);
    margin: 0 auto;
    width: min(60vw, 200px);
}

.detail--cont {
    width: min(60vw, 430px);
}

.detail--cont h1 {
    font-size: min(5vw, 34px);
    position: relative;
    right: 0;
}

.detail--cont h2 {
    font-size: min(4vw, 26px);

}

.lst {
    font-size: min(0.6em, 18px);
    width: fit-content;
    position: relative;
    left: 0;
    margin: 0;
    padding: 0;
    color: var(--primary-color);

}

.upc {
    font-size: 0.25em;
    display: none;
}

.ul-cont {
    /*width: fit-content;*/
    left: 0;
    position: relative;
    margin-top: 3vw;
    padding-top: 2vw;
    border-top: black solid 2px;
}

.detail--cont h4 {
    font-size: min(1.5em, 30px);
}

.alt-txt {
    font-size: min(0.8em, 20px);
    color: var(--primary-color);
}

.img--cont {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    column-gap: min(2vw, 24px);
    margin: 0 min(5vw, 200px) 0 auto;
    width: min(45vw, 325px);
    height: fit-content;
}

.opt-images {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.mini-img {
    cursor: pointer;
    width: min(10vw, 100px);
    height: min(10vw, 100px);
    border-radius: min(1em, 12px);
}

.etc {
    color: #eb0f80;
    text-align: center;
    margin-top: min(3vw, 18px);
    margin-bottom: min(3vw, 18px);
    font-size: min(1.25rem,36px);
}

