.product-image {
    border-radius: min(1rem,16px);
    width: 100%;
    box-shadow: 0rem 1rem 2rem var(--thirdary-color);
    /* height: 70vw; */
}

.product-div h2 {
    width: fit-content;
    margin: 0 auto;
    font-size: 2em;
    /* white-space: nowrap; */
}

.product-div p {
    width: fit-content;
    margin: 0 auto;
    font-size: 1.5em;
    /* color: black !important; */

    /* white-space: nowrap; */
}

.product-div {
    display: grid;
    row-gap: 1rem;
}

/*MEDIA_QUERIES*/
@media only screen and (min-width: 425px) {
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 540px) {

    .product-div {
        row-gap: 0px;
        text-align: center;
    }

    .product-div h2 {
        font-size: 32px;
    }

    .product-div p {
        font-size: 24px;
    }


}

